/****************************************** INPUT ********************************************/
/*------------------ INPUTS -------------------------------*/
.contacts input,
.contacts textarea {
  width: 100%;
  height: 50px;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border: 0.125rem solid #ffffff;
  color: #F5C5AA;
  border-radius: 10px;
  outline: 0 !important;
  box-shadow: none !important;
  margin-bottom: 1rem;
  padding-left: 0 !important;
}

.contacts .container {
  padding: 4rem 0;
}

.contacts input:focus,
.contacts textarea:focus {
  border: 1px #ffffff;
  padding-left: 20px;
}

.contacts textarea {
  height: 13rem !important;
  padding-left: 20px;
}

.contact-btn {
  margin: 1rem 0 0 0;
  width: 100%;
  height: 50px;
  font-size: 20px;
  text-transform: uppercase;
  border-radius: 10px;
  background: #ffffff;
  border: 0px;
  color: #F5C5AA
}
.contact-btn:hover{
  background: #F5C5AA;
  color: #ffffff;
}
input:focus + .line,
textarea:focus + .line {
  width: 100%;
  transition: width 0.5s ease-in-out;
  padding-left: 20px;
}

input.form-control {
  margin: 0.3rem 0;
  padding-left: 20px;
}

textarea.form-control {
  margin: 0.3rem 0;
}

.error-message {
  color: #ff0000;
  text-align: left;
}

.success-message {
  font-weight: 900;
  color: #00ff7f;
}

/*---------------- MEDIA -----------------------*/
@media (max-width: 768px) {
  .contacts {
    overflow-x: hidden;
  }

  .contacts .container {
    padding: 0 1rem;
  }

